import React from 'react';
import akram from '../assets/DSC_5607.JPG';

const About = () => {
  const education = [
    {
      title: 'Matrise en informatique',
      institution: "Universit du Qubec  Montral",
      date: 'Octobre 2024',
      location: 'Montral, QC, Canada',
    },
    {
      title: "Master en Technologie de linformation",
      institution: "Universit De Boumerds Mhamed Bougara",
      date: 'Septembre 2020',
      location: 'Boumerds, Algrie',
    },
    {
      title: "Licence en Ingnierie des Systmes dinformation et des Logiciels",
      institution: "Universit dAlger 1",
      date: 'Juillet 2018',
      location: 'Alger, Algrie',
    },
  ];

  const experiences = [
    {
      title: "Dmonstrateur et correcteur universitaire",
      institution: "Universit du Qubec  Montral",
      date: "Septembre 2021  Avril 2024",
      location: "Montral, QC, Canada",
      description: "Encadrement pratique des tudiants en programmation (Java, Matlab) et administration Linux, animation dateliers sur Git en contexte agile, et correction des examens pratiques."
    },
    {
      title: "Dveloppeur full stack",
      institution: "cole Nationale Polytechnique dAlger",
      date: "Fvrier 2020  Octobre 2020",
      location: "Alger, Algrie",
      description: "Dveloppement dune plateforme collaborative (Vue.js/Firebase) pour lautomatisation des processus institutionnels."
    },
    {
      title: "Dveloppeur dans un centre de recherche",
      institution: "Centre de Dveloppement des Technologies Avances",
      date: "Janvier 2018  Juillet 2018",
      location: "Alger, Algrie",
      description: "Modlisation et dveloppement dun logiciel pour la micro-gravure par laser et cration dun algorithme mtaheuristique pour loptimisation."
    },
  ];

  return (
    <div className='text-white max-w-[1200px] mx-auto' id='about'>
      <div className='md:grid md:grid-cols-3 sm:py-16 gap-4'>
        {/* Left Side - Education */}
        <div className='mt-4 md:mt-0 text-left'>
          <div className='my-auto mx-6 text-left'>
            <h2 className='text-3xl font-bold text-center mb-4 primary-color'>Parcours ducatif</h2>
            <ol className='relative border-l border-gray-300'>
              {education.map((item, index) => (
                <li key={index} className='mb-8 ml-4'>
                  <div className='absolute w-3 h-3 bg-blue-500 rounded-full -left-1.5 border border-white'></div>
                  <div className='p-3 border-gray-200 rounded-lg shadow-sm'>
                    <h3 className='text-md font-semibold'>{item.title}</h3>
                    <p className='text-gray-600'>{item.institution}</p>
                    <p className='text-sm text-gray-500'>{item.date}</p>
                    <p className='text-sm text-gray-500'>{item.location}</p>
                  </div>
                </li>
              ))}
            </ol>
          </div>
        </div>

        {/* Center - Image */}
        <div className='flex justify-center items-start'>
          <img className='rounded-3xl' width={400} height={400} src={akram} alt="Akram KHELILI" />
        </div>

        {/* Right Side - Experience */}
        <div className='mt-4 md:mt-0 text-left'>
          <div className='my-auto mx-6 text-left'>
            <h2 className='text-3xl font-bold text-center mb-4 primary-color'>Parcours professionnel</h2>
            <ol className='relative border-l border-gray-300'>
              {experiences.map((item, index) => (
                <li key={index} className='mb-8 ml-4'>
                  <div className='absolute w-3 h-3 bg-blue-500 rounded-full -left-1.5 border border-white'></div>
                  <div className='p-3 border-gray-200 rounded-lg shadow-sm'>
                    <h3 className='text-md font-semibold'>{item.title}</h3>
                    <p className='text-gray-600'>{item.institution}</p>
                    <p className='text-sm text-gray-500'>{item.date}</p>
                    <p className='text-sm text-gray-500'>{item.location}</p>
                    <p className='text-sm text-gray-500 mt-2'>{item.description}</p>
                  </div>
                </li>
              ))}
            </ol>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
